router.beforeEach((to, from, next) =>{
    next()
    let allowBack = true    //    true
    if (to.meta.allowBack !== undefined ) {
        allowBack = to.meta.allowBack
    }
    if (!allowBack) {
        history.pushState(null, null, '')
    } 
})
